.header {
  background-color: var(--color-background-secondary);
  color: var(--color-text-light);

  & .container {
    padding-top: 10px;
    padding-bottom: 10px;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &_holder {
    display: flex;
    align-items: center;
  }

  &_open-slide-menu-btn {
    width: 24px;
    height: 24px;
    position: relative;
    z-index: 2;
    overflow: hidden;
    & span {
      width: 24px;
      height: 2px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: var(--color-text-light);

      &:nth-of-type(2) {
        top: calc(50% - 5px);
      }
      &:nth-of-type(3) {
        top: calc(50% + 5px);
      }
    }
  }

  &_close-slide-menu-btn {
    width: 24px;
    height: 24px;
    position: relative;
    z-index: 2;
    overflow: hidden;
    & span {
      width: 24px;
      height: 2px;
      position: absolute;
      top: 50%;
      left: 50%;
      background-color: var(--color-text-dark-primary);

      transform: translate(-50%, 0%) rotate(45deg);
      &:nth-of-type(2) {
        transform: translate(-50%, 0%) rotate(-45deg);
      }
    }
  }

  &_contact-link {
    padding: 8px 16px;
    background-color: var(--color-background-secondary);
    color: var(--color-text-light);

    @include transition-for-2(background-color, color);

    &:hover,
    &:focus {
      background-color: var(--color-background-primary);
      color: var(--color-background-secondary);
    }
  }

  .logo-holder {
    margin-left: 16px;
  }
}

.header-slide-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  width: 100%;
  height: 100%;
  padding: 12px 16px;

  overflow: scroll;
  transform: translateX(-100%);
  @include transition-for-1(transform);

  background-color: var(--color-background-primary);

  &.active {
    transform: translateX(0);
  }

  &_holder {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &_title-holder {
    display: flex;
    flex-direction: row;
    align-items: center;

    .arrow-down {
      margin-left: 8px;
    }
  }

  &_list {
    margin-top: 18px;
  }

  &_item {
    // display: flex;
    // align-items: center;
    // justify-content: flex-start;

    color: var(--color-text-slide-menu);
    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
  }

  &_item:not(:last-child) {
    margin-bottom: 24px;
  }

  &_subitem-icon {
    width: 24px;
    height: 24px;
  }

  &_item-list {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
    color: var(--color-text-dark-primary);
  }

  &_subitem {
    display: flex;
    align-items: center;

    padding-top: 12px;
    padding-bottom: 12px;
  }
  &_subitem:not(:last-child) {
    border-bottom: 1px solid var(--color-border-header-slide-menu);
  }

  &_subitem-icon {
    margin-bottom: 0;
    margin-right: 8px;
  }
}
