:root {
  --color-background-primary: #fff;
  --color-background-secondary: #05058c;
  --color-background-question: #f4f2fa;
  --color-background-tag-content: #ccccd6;
  --color-accent: #05058c;
  --color-accent-secondary: #100fa8;
  --color-text-light: #f4f2fa;
  --color-text-dark-primary: #003;
  --color-text-dark-secondary: #4d4d70;
  --color-text-white: #fff;
  --color-text-slide-menu: #100fa8;
  --color-border-header-slide-menu: #ccccd6;
  --color-border-solutions: #ccccd6;
  --color-border-feedback: #ccccd6;
  --color-border-footer: #4d4d70;
  --color-swiper-scrollbar-disable: #cecffb;
  --color-swiper-scrollbar-enable: #fff;
  --color-swiper-button-disable: #cecffb;
  --color-swiper-button-enable: #fff;
  --color-feedback-swiper-scrollbar-disable: #ccccd6;
  --color-feedback-swiper-scrollbar-enable: #05058c;
  --color-feedback-swiper-button-disable: #ccccd6;
  --color-feedback-swiper-button-enable: #05058c;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--color-background-primary);
  color: var(--color-text-dark-primary);
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25;
  position: relative;
}

body.block-scroll {
  overflow: hidden;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :after, :before {
  box-sizing: inherit;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 780px) {
  .container {
    max-width: 780px;
    padding: 0 40px;
  }
}

@media screen and (min-width: 1540px) {
  .container {
    max-width: 1540px;
    padding: 0 87px;
  }
}

.section {
  padding-top: 64px;
  padding-bottom: 64px;
}

.toggle {
  display: none;
}

.contact-link {
  background-color: var(--color-background-primary);
  color: var(--color-text-dark-primary);
  border: 1px solid var(--color-text-light);
  border-radius: 4px;
  padding: 10px 20px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.contact-link:hover, .contact-link:focus {
  color: var(--color-background-primary);
  background-color: var(--color-accent-secondary);
}

.solution-link {
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.71;
  text-decoration: none;
  transition: -webkit-text-decoration .25s cubic-bezier(.4, 0, .2, 1), text-decoration .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.solution-link .arrow-right {
  margin-left: 8px;
}

.solution-link:hover, .solution-link:focus {
  color: var(--color-accent-secondary);
  text-decoration: underline;
}

.btn {
  cursor: pointer;
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
}

.arrow-down {
  width: 24px;
  height: 24px;
  outline: 2px solid salmon;
}

.arrow-down svg {
  width: 24px;
  height: 24px;
}

.arrow-right {
  width: 16px;
  height: 16px;
  outline: 2px solid #2e8b57;
  display: inline-flex;
}

.arrow-right svg {
  width: 16px;
  height: 16px;
}

.title {
  text-align: center;
  font-family: Tiempos Headline;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.primary-font_title {
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}

.swiper-horizontal > .swiper-scrollbar, .swiper-scrollbar.swiper-scrollbar-horizontal {
  background-color: var(--color-swiper-scrollbar-disable);
}

.swiper-scrollbar.swiper-scrollbar-horizontal > .swiper-scrollbar-drag {
  background-color: var(--color-swiper-scrollbar-enable);
}

.swiper-horizontal > .swiper-scrollbar, .swiper-scrollbar.swiper-scrollbar-horizontal {
  width: 70% !important;
  bottom: 10px !important;
  left: 15% !important;
}

.swiper-button-next:after, .swiper-button-next:after, .swiper-button-prev:after {
  color: var(--color-swiper-button-enable);
}

.swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
  color: var(--color-swiper-button-disable);
}

.swiper-button-next, .swiper-button-prev {
  top: 98% !important;
}

.swiper-button-next:after, .swiper-button-prev:after {
  font-size: 24px !important;
}

.swiper-button-prev, .swiper-rtl .swiper-button-next {
  left: 0 !important;
}

.swiper-button-next, .swiper-rtl .swiper-button-prev {
  right: 0 !important;
}

.header {
  background-color: var(--color-background-secondary);
  color: var(--color-text-light);
}

.header .container {
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.header_holder {
  align-items: center;
  display: flex;
}

.header_open-slide-menu-btn {
  width: 24px;
  height: 24px;
  z-index: 2;
  position: relative;
  overflow: hidden;
}

.header_open-slide-menu-btn span {
  width: 24px;
  height: 2px;
  background-color: var(--color-text-light);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header_open-slide-menu-btn span:nth-of-type(2) {
  top: calc(50% - 5px);
}

.header_open-slide-menu-btn span:nth-of-type(3) {
  top: calc(50% + 5px);
}

.header_close-slide-menu-btn {
  width: 24px;
  height: 24px;
  z-index: 2;
  position: relative;
  overflow: hidden;
}

.header_close-slide-menu-btn span {
  width: 24px;
  height: 2px;
  background-color: var(--color-text-dark-primary);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%)rotate(45deg);
}

.header_close-slide-menu-btn span:nth-of-type(2) {
  transform: translate(-50%)rotate(-45deg);
}

.header_contact-link {
  background-color: var(--color-background-secondary);
  color: var(--color-text-light);
  padding: 8px 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.header_contact-link:hover, .header_contact-link:focus {
  background-color: var(--color-background-primary);
  color: var(--color-background-secondary);
}

.header .logo-holder {
  margin-left: 16px;
}

.header-slide-menu {
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: var(--color-background-primary);
  padding: 12px 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
  transform: translateX(-100%);
}

.header-slide-menu.active {
  transform: translateX(0);
}

.header-slide-menu_holder {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-slide-menu_title-holder {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.header-slide-menu_title-holder .arrow-down {
  margin-left: 8px;
}

.header-slide-menu_list {
  margin-top: 18px;
}

.header-slide-menu_item {
  color: var(--color-text-slide-menu);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.14;
}

.header-slide-menu_item:not(:last-child) {
  margin-bottom: 24px;
}

.header-slide-menu_subitem-icon {
  width: 24px;
  height: 24px;
}

.header-slide-menu_item-list {
  color: var(--color-text-dark-primary);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.14;
}

.header-slide-menu_subitem {
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  display: flex;
}

.header-slide-menu_subitem:not(:last-child) {
  border-bottom: 1px solid var(--color-border-header-slide-menu);
}

.header-slide-menu_subitem-icon {
  margin-bottom: 0;
  margin-right: 8px;
}

