.solutions-subtitle {
  margin-bottom: 15px;
  font-size: 16px;
  font-weight: 700;
}

.solutions_title {
  padding-bottom: 64px;
}

.solutions_icon {
  width: 24px;
  height: 24px;
  outline: 2px solid #00f;
