}

.article-img {
  max-width: 287px;
  border-radius: 16px;
  margin-bottom: 32px;
  overflow: hidden;
}

.article-title {
  text-align: left;
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 1.24;
}

.article-text {
  margin-bottom: 24px;
}

.article-link-holder {
  justify-content: center;
  margin-bottom: 32px;
  display: flex;
}

.article-link {
  background-color: var(--color-background-primary);
  color: var(--color-accent-secondary);
  border-radius: 4px;
  padding: 8px 44px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.33;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.article-link:hover, .article-link:focus {
  color: var(--color-background-primary);
  background-color: var(--color-accent-secondary);
}

.feedback_title {
  margin-bottom: 32px;
}

.feedback-holder {
  border: 1px solid var(--color-border-feedback);
  border-radius: 16px;
  margin-bottom: 24px;
  padding: 16px;
