}

.feedback_company-logo {
  justify-content: center;
  margin-bottom: 16px;
  display: flex;
}

.feedback_company-logo img {
  width: 163px;
  height: 40px;
}

.feedback-text {
  text-align: center;
  margin-bottom: 60px;
}

.feedback_link {
  justify-content: center;
}

.feedback_link .arrow-right {
  margin-left: 22px;
}

.feedback_client-holder {
  min-width: 127px;
  color: var(--color-text-dark-secondary);
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.33;
  display: flex;
}

.feedback_client-image {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  flex-shrink: 0;
  margin-right: 8px;
  overflow: hidden;
}

.feedback_client-position {
