}

.feedback .swiper {
  min-height: 450px !important;
}

.feedback .swiper-horizontal > .swiper-scrollbar, .feedback .swiper-scrollbar.swiper-scrollbar-horizontal {
  background-color: var(--color-feedback-swiper-scrollbar-disable);
}

.feedback .swiper-scrollbar.swiper-scrollbar-horizontal > .swiper-scrollbar-drag {
  background-color: var(--color-feedback-swiper-scrollbar-enable);
}

.feedback .swiper-button-next:after, .feedback .swiper-button-next:after, .feedback .swiper-button-prev:after {
  color: var(--color-feedback-swiper-button-enable);
}

.feedback .swiper-button-next.swiper-button-disabled, .feedback .swiper-button-prev.swiper-button-disabled {
  color: var(--color-feedback-swiper-button-disable);
}

